import { useState, useEffect, useRef } from "react";
import { Link, useNavigate } from "react-router-dom";
import back from "../../Assets/images/back.png";
import corporate from "../../Assets/images/Mask Group -1.png";
import "./index.css";
import { BsPersonPlus } from "react-icons/bs";
import Axios from "axios";
import { useSelector } from "react-redux";

const UpdatePartners = () => {
  const items = localStorage.getItem("user");
  const obj = JSON.parse(items);
  const user_id = obj._id;

  const [partnersInfo, setPartnersInfo] = useState([]);
  const BASE_API_URL = useSelector((state) => state.BASE_API_URL);
  const ref1 = useRef();
  useEffect(() => {
    Axios.get(
      `${BASE_API_URL}/api/partners/partners-info?company_id=63ac5ac5143374bfb916e85b`
    )
      .then((res) => {
        // setPartnersInfo(res.data);
        console.log(res.data);
      })
      .catch((err) => console.log(err));
  }, [BASE_API_URL]);

  const handleSubmit = (e) => {
    e.preventDefault();
    Axios.post(`${BASE_API_URL}/api/partners/update-partner-info/${user_id}`, {
      partners_info: partnersInfo,
    })
      .then((res) => {
        console.log(res.data);
      })
      .catch((err) => console.log(err));
  };
  return (
    <div className="add_parteners">
      <header>
        <Link to="/register">
          <span>Back</span>
          <img src={back} alt="" />
        </Link>
      </header>
      <section className="register">
        <header>
          <span className="icon">
            <BsPersonPlus />
          </span>
          <span className="text">Update partners</span>
        </header>
        <form className="form-container">
          <div className="body">
            {partnersInfo.map((partnerInfo, index) => (
              <div className="my-4">
                <h3
                  className="my-2"
                  style={{ color: "#fff", borderBottom: "2px solid #fff" }}
                >
                  {" "}
                  Information Partner({index + 1})
                </h3>
                <div className="my-3 input_">
                  <input
                    value={partnerInfo.full_name}
                    type="text"
                    placeholder="Partner's full name"
                  />
                </div>
                <div className="my-3 input_">
                  <input
                    value={partnerInfo.birthday}
                    className="input"
                    ref={ref1}
                    onFocus={() => (ref1.current.type = "date")}
                    onBlur={() => (ref1.current.type = "text")}
                    placeholder="Born date"
                    type="text"
                  />
                </div>
                <div className="my-3 input_">
                  <input
                    value={partnerInfo.phone_number}
                    type="number"
                    placeholder="Personal mobile number"
                  />
                </div>
                <div className="my-3 input_">
                  <input
                    value={partnerInfo.whatsapp_number}
                    type="number"
                    placeholder="Whatsapp number"
                  />
                  <p></p>
                </div>
                <div className="my-3 input_">
                  <input
                    value={partnerInfo.landline_number}
                    type="number"
                    placeholder="Landline extention"
                  />
                </div>
                <div className="my-3 input_">
                  <input
                    value={partnerInfo.email}
                    type="email"
                    placeholder="Personal email address"
                  />
                </div>
                <div className="my-3 input_">
                  <input
                    value={partnerInfo.participation_rate}
                    type="number"
                    placeholder="Participation rate"
                  />
                </div>
              </div>
            ))}
          </div>
          <div className="footer">
            <button className="mt-3" onClick={handleSubmit}>
              Submit
            </button>
          </div>
        </form>
      </section>
      <div className="background2">
        <img src={corporate} alt="" />
      </div>
    </div>
  );
};

export default UpdatePartners;
